body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(to bottom, rgb(0, 14, 53) 0%, rgb(0, 215, 223) 100%);
}




@font-face {
    font-family: "SEGA LOGO FONT";
    font-style: normal;
    font-weight: normal;
    src: local("SEGA LOGO FONT"), url(/meet-robot/static/media/SEGA.12a3db8b.woff) format("woff");
}

h1 {
    font-family: "SEGA LOGO FONT";
    font-weight: 200;
    color: rgb(150, 204, 255);
}

